define('bloggly/router', ['exports', 'ember', 'bloggly/config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router = Ember['default'].Router.extend({
    location: config['default'].locationType
  });

  Router.map(function () {
    this.resource('posts');
  });

  exports['default'] = Router;

});